'use client';

import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { motion, useMotionValue, useTransform } from 'framer-motion';
import { Card, Button, Empty, Spin, Typography, Image } from 'antd';
import { HeartOutlined, CloseOutlined, SyncOutlined } from '@ant-design/icons';

// === SIMULATED DATA ===
const generateFakeUsers = (count: number): UserProfile[] => {
    const names = ['Alex', 'Sam', 'Jordan', 'Taylor', 'Casey', 'Morgan', 'Riley', 'Avery'];
    const cities = ['San Francisco', 'New York', 'Austin', 'Seattle', 'Miami', 'Denver'];
    return Array.from({ length: count }, (_, i) => ({
        id: `user-${Date.now()}-${i}`,
        type: 'user' as const,
        user_account_information: {
            user_account_first_name: names[i % names.length],
            user_account_profile_picture_url: `https://i.pravatar.cc/400?u=${i}`,
            age: 24 + (i % 8),
            city: cities[i % cities.length],
        },
    }));
};

const generateFakeAd = (): UserProfile => ({
    id: `ad-${Date.now()}`,
    type: 'ad' as const,
    user_account_information: {
        user_account_first_name: 'Sponsored',
    },
    nativeAd: {
        headline: 'Try Premium!',
        body: 'Get unlimited likes and see who likes you',
        cta: 'Upgrade Now',
    },
});

interface UserProfile {
    id: string;
    type: 'user' | 'ad';
    user_account_information: {
        user_account_first_name: string;
        user_account_profile_picture_url?: string;
        age?: number;
        city?: string;
    };
    nativeAd?: {
        headline: string;
        body: string;
        cta: string;
    };
}

// === SWIPE COMPONENT ===
export default function SwipePage() {
    const [profiles, setProfiles] = useState<UserProfile[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [swipeCount, setSwipeCount] = useState(0);

    const swipeX = useMotionValue(0);
    const swipeY = useMotionValue(0);
    const rotate = useTransform(swipeX, [-150, 0, 150], [-25, 0, 25]);
    const opacityLeft = useTransform(swipeX, [-120, -60], [1, 0]);
    const opacityRight = useTransform(swipeX, [60, 120], [0, 1]);

    const pendingSwipe = useRef<'left' | 'right' | null>(null);
    const isDragging = useRef(false);

    const SWIPE_THRESHOLD = 120;
    const SWIPE_OUT_DISTANCE = 500;
    const AD_FREQUENCY = 8;

    // Simulate initial load
    useEffect(() => {
        setTimeout(() => {
            setProfiles(generateFakeUsers(5));
            setIsLoading(false);
        }, 1500);
    }, []);

    // Auto-refresh when empty
    useEffect(() => {
        if (profiles.length === 0 && !isLoading) {
            const timer = setTimeout(() => {
                setIsLoading(true);
                setSwipeCount(0);
                setTimeout(() => {
                    setProfiles(generateFakeUsers(6));
                    setIsLoading(false);
                }, 1200);
            }, 1000);
            return () => clearTimeout(timer);
        }
    }, [profiles.length, isLoading]);

    // Ad injection
    const injectAd = useCallback(() => {
        setProfiles((prev) => [generateFakeAd(), ...prev]);
    }, []);

    // Swipe action
    const handleSwipe = useCallback(
        (direction: 'left' | 'right') => {
            const top = profiles[0];
            if (top?.type === 'user') {
                console.log(`Swiped ${direction} on ${top.user_account_information.user_account_first_name}`);
            }

            setSwipeCount((c) => {
                const next = c + 1;
                if (next % AD_FREQUENCY === 0) {
                    setTimeout(injectAd, 300);
                }
                return next;
            });

            setProfiles((prev) => prev.slice(1));
        },
        [profiles, injectAd]
    );

    // Drag end
    const onDragEnd = () => {
        isDragging.current = false;
        const x = swipeX.get();
        if (Math.abs(x) > SWIPE_THRESHOLD) {
            const direction = x > 0 ? 'right' : 'left';
            pendingSwipe.current = direction;
            swipeX.set(direction === 'right' ? SWIPE_OUT_DISTANCE : -SWIPE_OUT_DISTANCE);
            setTimeout(() => {
                if (pendingSwipe.current === direction) {
                    handleSwipe(direction);
                    pendingSwipe.current = null;
                }
            }, 300);
        } else {
            swipeX.set(0);
            swipeY.set(0);
        }
    };

    // Button swipe
    const handleChoice = (direction: 'left' | 'right') => {
        if (profiles.length === 0) return;
        swipeX.set(direction === 'right' ? SWIPE_OUT_DISTANCE : -SWIPE_OUT_DISTANCE);
        setTimeout(() => handleSwipe(direction), 300);
    };

    // Polling for swipe completion
    useEffect(() => {
        const interval = setInterval(() => {
            if (pendingSwipe.current && !isDragging.current) {
                handleSwipe(pendingSwipe.current);
                pendingSwipe.current = null;
            }
        }, 16);
        return () => clearInterval(interval);
    }, [handleSwipe]);

    const visibleProfiles = useMemo(() => profiles.slice(0, 3), [profiles]);
    const hasProfiles = visibleProfiles.length > 0;

    // Card Component
    const ProfileCard = ({ profile, index }: { profile: UserProfile; index: number }) => {
        const isTop = index === 0;
        const zIndex = visibleProfiles.length - index;

        if (profile.type === 'ad') {
            return (
                <motion.div
                    style={{
                        position: 'absolute',
                        width: '100%',
                        maxWidth: 380,
                        zIndex,
                    }}
                    initial={{ scale: 0.95 - index * 0.03 }}
                    animate={{ scale: 0.95 - index * 0.03 }}
                >
                    <Card
                        style={{
                            borderRadius: 16,
                            overflow: 'hidden',
                            boxShadow: '0 8px 30px rgba(0,0,0,0.15)',
                        }}
                    >
                        <div style={{ padding: 24, textAlign: 'center' }}>
                            <Typography.Title level={4} style={{ margin: '0 0 8px' }}>
                                {profile.nativeAd?.headline}
                            </Typography.Title>
                            <Typography.Text type="secondary">
                                {profile.nativeAd?.body}
                            </Typography.Text>
                            <Button type="primary" style={{ marginTop: 16 }}>
                                {profile.nativeAd?.cta}
                            </Button>
                        </div>
                    </Card>
                </motion.div>
            );
        }

        return (
            <motion.div
                style={{
                    position: 'absolute',
                    width: '100%',
                    maxWidth: 380,
                    zIndex,
                    x: isTop ? swipeX : 0,
                    y: isTop ? swipeY : 0,
                    rotate: isTop ? rotate : 0,
                }}
                drag={isTop ? 'x' : false}
                dragConstraints={{ left: 0, right: 0 }}
                onDragStart={() => (isDragging.current = true)}
                onDragEnd={onDragEnd}
                initial={{ scale: 0.95 - index * 0.03 }}
                animate={{ scale: 0.95 - index * 0.03 }}
                transition={{ type: 'spring', stiffness: 300, damping: 30 }}
            >
                <Card
                    style={{
                        borderRadius: 16,
                        overflow: 'hidden',
                        boxShadow: '0 8px 30px rgba(0,0,0,0.15)',
                        cursor: isTop ? 'grab' : 'default',
                    }}
                    bodyStyle={{ padding: 0 }}
                >
                    <div style={{ position: 'relative' }}>
                        <Image
                            src={profile.user_account_information.user_account_profile_picture_url || '/placeholder.png'}
                            alt={profile.user_account_information.user_account_first_name}
                            width={380}
                            height={480}
                            style={{ width: '100%', height: 'auto', display: 'block' }}
                            preview={false}
                        />

                        {/* Swipe Indicators */}
                        {isTop && (
                            <>
                                <div
                                    style={{
                                        position: 'absolute',
                                        top: 30,
                                        left: 30,
                                        background: 'rgba(255,59,48,0.9)',
                                        color: 'white',
                                        padding: '10px 20px',
                                        borderRadius: 12,
                                        fontWeight: 'bold',
                                        fontSize: 20,
                                        opacity: opacityLeft.get(),
                                        transform: 'rotate(-20deg)',
                                    }}
                                >
                                    NOPE
                                </div>
                                <div
                                    style={{
                                        position: 'absolute',
                                        top: 30,
                                        right: 30,
                                        background: 'rgba(52,199,89,0.9)',
                                        color: 'white',
                                        padding: '10px 20px',
                                        borderRadius: 12,
                                        fontWeight: 'bold',
                                        fontSize: 20,
                                        opacity: opacityRight.get(),
                                        transform: 'rotate(20deg)',
                                    }}
                                >
                                    LIKE
                                </div>
                            </>
                        )}

                        {/* Profile Info */}
                        <div style={{ padding: 20, background: 'white' }}>
                            <Typography.Title level={3} style={{ margin: 0 }}>
                                {profile.user_account_information.user_account_first_name}, {profile.user_account_information.age}
                            </Typography.Title>
                            <Typography.Text type="secondary">
                                {profile.user_account_information.city}
                            </Typography.Text>
                        </div>
                    </div>
                </Card>
            </motion.div>
        );
    };

    // Loading State
    if (isLoading) {
        return (
            <div
                style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    height: '100vh',
                    background: '#f5f5f5',
                }}
            >
                <Spin size="large" />
                <Typography.Text style={{ marginTop: 16, fontSize: 16 }}>
                    Finding people near you...
                </Typography.Text>
            </div>
        );
    }

    return (
        <div
            style={{
                minHeight: '100vh',
                background: '#f5f5f5',
                padding: '20px 0',
                position: 'relative',
                fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
            }}
        >
            <div
                style={{
                    maxWidth: 420,
                    margin: '0 auto',
                    position: 'relative',
                    height: '80vh',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}
            >
                {hasProfiles ? (
                    <>
                        {visibleProfiles.map((profile, index) => (
                            <ProfileCard key={profile.id} profile={profile} index={index} />
                        ))}

                        {/* Action Buttons */}
                        <div
                            style={{
                                position: 'absolute',
                                bottom: -100,
                                left: 0,
                                right: 0,
                                display: 'flex',
                                justifyContent: 'center',
                                gap: 50,
                            }}
                        >
                            <Button
                                shape="circle"
                                size="large"
                                icon={<CloseOutlined />}
                                style={{
                                    width: 64,
                                    height: 64,
                                    fontSize: 28,
                                    background: '#fff',
                                    border: '2px solid #ddd',
                                    boxShadow: '0 4px 16px rgba(0,0,0,0.1)',
                                }}
                                onClick={() => handleChoice('left')}
                            />
                            <Button
                                shape="circle"
                                size="large"
                                icon={<HeartOutlined />}
                                type="primary"
                                style={{
                                    width: 64,
                                    height: 64,
                                    fontSize: 28,
                                    boxShadow: '0 4px 16px rgba(255, 77, 79, 0.3)',
                                }}
                                onClick={() => handleChoice('right')}
                            />
                        </div>
                    </>
                ) : (
                    <Empty
                        image={
                            <div
                                style={{
                                    width: 120,
                                    height: 120,
                                    borderRadius: '50%',
                                    background: '#ddd',
                                    backgroundImage: 'ur[](https://i.pravatar.cc/300?u=self)',
                                    backgroundSize: 'cover',
                                }}
                            />
                        }
                        description={
                            <Typography.Text style={{ fontSize: 18, color: '#555' }}>
                                No one new around you.
                            </Typography.Text>
                        }
                    >
                        <Button
                            type="primary"
                            icon={<SyncOutlined />}
                            onClick={() => {
                                setIsLoading(true);
                                setTimeout(() => {
                                    setProfiles(generateFakeUsers(5));
                                    setIsLoading(false);
                                }, 1200);
                            }}
                        >
                            Try Again
                        </Button>
                    </Empty>
                )}
            </div>
        </div>
    );
}